.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    max-height: 200px;
    margin-right: 10px;
    cursor: pointer;
}

.small_poster{
    max-height: 150px;
    margin-right: 10px;
    cursor: pointer;
}
.poster:hover{
    transform: scale(1.1);
}
.small_poster:hover{
    transform: scale(1.1);
}